.Container {
    overflow: hidden;
}

.GridContainer {
    display: grid;
    grid-gap: 10px;
    padding: 10px;
    align-content: center;
    justify-content: center;
    font-size: 25px;
    overflow: hidden;
}

.Cart {
    background: white;
    width: 700px;
    height: 200px;
    border-radius: 100% 100% 100% 100% / 10%;
    overflow: hidden;
    box-shadow: 0 8px 5px #777;
    display: grid;
    grid-template-columns: 1fr 3fr;
}

.Cart img {
    width: 200px;
    height: 200px;
    border-radius: 5px;
    box-shadow: 0 10px 35px #777;
}

.Details {
    padding: 10px;
    margin: 30px;
}

@media (max-width: 757px) {
    .Cart img {
        width: 200px;
        height: 180px;
        border-radius: 5px;
        box-shadow: 0 10px 35px #777;
    }
    .GridContainer {
        font-size: 20px;
    }
    .Cart {
        width: 600px;
        height: 180px;
        border-radius: 100% 100% 100% 100% / 10%;
    }
}

@media (max-width: 655px) {
    .GridContainer {
        font-size: 18px;
    }
    .Cart {
        width: 550px;
        height: 170px;
        border-radius: 100% 100% 100% 100% / 10%;
    }
    .Cart img {
        width: 180px;
        height: 170px;
        border-radius: 5px;
        box-shadow: 0 10px 35px #777;
    }
}

@media (max-width: 575px) {
    .GridContainer {
        font-size: 15px;
    }
    .Cart {
        width: 400px;
        height: 150px;
        border-radius: 100% 100% 100% 100% / 10%;
    }
}

@media (max-width: 430px) {
    .Cart img {
        width: 100px;
        height: 100px;
        border-radius: 5px;
        box-shadow: 0 10px 35px #777;
    }
    .GridContainer {
        font-size: 10px;
    }
    .Cart {
        width: 300px;
        height: 100px;
        border-radius: 100% 100% 100% 100% / 10%;
    }
    .Details {
        padding: 5px;
        margin: 15px;
    }
}

@media (max-width: 430px) {
    .Cart img {
        width: 90px;
        height: 120px;
        border-radius: 5px;
        box-shadow: 0 10px 35px #777;
    }
    .GridContainer {
        font-size: 8px;
    }
    .Cart {
        width: 250px;
        height: 120px;
        border-radius: 100% 100% 100% 100% / 10%;
    }
    .Details {
        padding: 5px;
        margin: 10px;
    }
}